import React from 'react';
import {List,ListItem,Divider} from '@material-ui/core'
import Popper from '@material-ui/core/Popper';
import Typography from '@material-ui/core/Typography';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';

export default function SidebarSection(props){
    return (
      <div>
        <List>
          <SidebarItem title={true} text={props.title}/>
          {props.list.map((text, index) => (
            <SidebarItem text={text} key={index}/>
          ))}
          </List>
        <Divider />
      </div>
  )
}

const useStyles = makeStyles(theme => ({
  root: {
    width: 500,
  },
  typography: {
    padding: theme.spacing(2),
    backgroundColor: "white",
    color:"black",
    border: "1px solid black",
  },
}));

function SidebarItem(props){
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [open, setOpen] = React.useState(false);
    const [placement, setPlacement] = React.useState();
    const classes = useStyles();

    const mouseEnter = newPlacement => e => {
        setAnchorEl(e.currentTarget);
        setOpen(true);
        setPlacement(newPlacement)
      }
    const mouseLeave = newPlacement => e => {
        setAnchorEl(e.currentTarget)
        setOpen(false)
        setPlacement(newPlacement)
    }
    return (
        <div>
          <Popper open={open} anchorEl={anchorEl} placement={placement} transition>
          {({ TransitionProps }) => (
            <Fade {...TransitionProps} timeout={350}>
              <Paper>
                <Typography className={classes.typography}>
                  {/* {props.text} */} This is a work in progress / very experimental. It should briefly describe my experience with this item.
                </Typography>
              </Paper>
            </Fade>
          )}
        </Popper>
          <ListItem className="sidebar-item" onMouseEnter={mouseEnter("right")} onMouseLeave={mouseLeave("right")}>        
              {props.title ? <b>{props.text}</b> : props.text}
            </ListItem>
          </div>
      )
}